import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {fetchSKUList, selectSKU} from "../actions/sku";
import GTIN from "../GTIN";
import {ellipses} from "../utils";
import TrimmedText from "./TrimmedText";
import ProgressBar from "../chums-components/ProgressBar";
import SortableTable from "../chums-components/SortableTable";

const tableFields = [
    {field: 'sku', title: 'SKU'},
    {field: 'description', title: 'Description'},
    {field: 'upc', title: 'UPC', render: (row) => GTIN.format(row.upc), className: 'upc'},
    {field: 'notes', title: 'Notes', render: ({notes}) => (<TrimmedText text={notes} length={25}/>)}
];

class SKUSystemList extends Component {
    static propTypes = {
        list: PropTypes.array,
        selected: PropTypes.shape({
            id: PropTypes.number,
        }),
        loading: PropTypes.bool,
        selectedGroup: PropTypes.shape({
            id: PropTypes.number,
        }),
        filter: PropTypes.string,
        showInactive: PropTypes.bool,
        fetchSKUList: PropTypes.func,
        selectSKU: PropTypes.func,
    };

    static defaultProps = {
        list: [],
        selected: {
            id: 0,
        },
        loading: false,
        selectedGroup: {
            id: 0,
        },
        filter: '',
        showInactive: false,
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.selectedGroup.id !== this.props.selectedGroup.id && this.state.page !== 1) {
            this.setState({page: 1});
        }
    }

    state = {
        rowsPerPage: 10,
        page: 1,
    };

    render() {
        const {list, selected, loading, filter, showInactive} = this.props;
        const {rowsPerPage, page} = this.state;

        let reFilter, upcFilter;
        try {
            reFilter = new RegExp('\\b' + filter, 'i');
            upcFilter = new RegExp(filter, 'i');
        } catch (err) {
            reFilter = /./i;
            upcFilter = /./i;
        }

        const rows = list
            .filter(sku => showInactive || sku.active || sku.id === 0)
            .filter(sku => filter.trim() === ''
                || sku.id === 0
                || reFilter.test(sku.sku)
                || upcFilter.test(sku.upc)
                || reFilter.test(sku.description)
                || reFilter.test(sku.notes || ''));

        return (
            <Fragment>
                {loading && <ProgressBar striped={true} style={{height: '5px'}}/>}
                <SortableTable fields={tableFields} data={rows} displayPageSelectorInline={false}
                               rowsPerPage={rowsPerPage}
                               onChangeRowsPerPage={(rowsPerPage) => this.setState({rowsPerPage, page: 1})}
                               page={page} onChangePage={(page) => this.setState({page})}
                               onChangeSort={() => this.setState({page: 1})}
                               onSelect={this.props.selectSKU}
                               defaultSort="sku"
                />
            </Fragment>
        );
    }
}

const mapStateToProps = (state) => {
    const {sku} = state;
    const { list, selected, loading, selectedGroup } = sku;
    return { list, selected, loading, selectedGroup };
};

const mapDispatchToProps = {
    fetchSKUList,
    selectSKU
};

export default connect(mapStateToProps, mapDispatchToProps)(SKUSystemList) 
