import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {NAV_TABS} from "../constants";
import {loadPermissions} from "../actions/app";
import {connect} from "react-redux";
import {NavLink} from 'react-router-dom';
import {fetchGroups} from "../actions/groups";

const Tab = ({path, title}) => {
    return (
        <li role="presentation" className="nav-item">
            <NavLink to={path} className="nav-link">{title}</NavLink>
        </li>
    );
};

class TabList extends Component {
    static propTypes = {
        fetchGroups: PropTypes.func,
        loadPermissions: PropTypes.func,
    };

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.loadPermissions();
        this.props.fetchGroups();
    }

    render() {
        return (
            <ul className="nav nav-tabs">
                {NAV_TABS.map((tab, index) => <Tab key={index} {...tab} />)}
            </ul>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    const {alerts, currentTab} = state.app;
    return {alerts, currentTab};
};

const mapDispatchToProps = {
    loadPermissions,
    fetchGroups,
};

export default connect(mapStateToProps, mapDispatchToProps)(TabList);

