import {
    RECEIVE_COLOR,
    RECEIVE_COLORS, RECEIVE_GROUP,
    RECEIVE_GROUPS,
    SELECT_COLOR,
    SELECT_GROUP,
    UPDATE_COLOR,
    UPDATE_GROUP
} from "../constants";
import {fetchGET, fetchPOST} from "../chums-components/fetch";
import {setAlert} from "./app";


export const selectGroup = (group) => ({type: SELECT_GROUP, group});

export const fetchGroups = () => (dispatch, getState) => {
    fetchGET('/node-dev/sku/groups')
        .then(res => {
            const groups = res.result
                .map(g => ({...g, notes: g.notes || '', key: g.id}));
            dispatch({type: RECEIVE_GROUPS, groups});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error loading groups: ${err.message}`}));
        });
};

export const updateGroup = (field, value) => ({type: UPDATE_GROUP, field, value});

export const saveGroup = (group) => (dispatch, getState) => {
    const state = getState();
    if (!state.app.isAdmin) {
        return;
    }
    const url = '/node-dev/sku/groups';
    fetchPOST(url, group)
        .then(res => {
            const [group] = res.result;
            group.key = group.id;
            group.notes = group.notes || '';
            dispatch({type: RECEIVE_GROUP, group});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error saving group: ${err.message}`}))
        });

};
