import React, {Component} from 'react';
import { Provider } from 'react-redux';
import {HashRouter as Router, Route, Redirect} from 'react-router-dom';
import PropTypes from 'prop-types';
import {version} from "../../package";
import ColorsTab from './ColorsTab';
import MixesTab from './MixesTab';
import GroupsTab from './GroupsTab';
import SKUSystemTab from './SKUSystemTab';
import UPCByColorTab from './UPCByColorTab';
import TabList from "./TabList";
import {TAB_BY_COLOR, TAB_COLORS, TAB_GROUPS, TAB_MIXES, TAB_SKUS, TAB_CATEGORIES} from "../constants";
import {fetchSettings} from "../actions/settings";
import CategoriesTab from "./CategoriesTab";
import AlertList from "../chums-components/AlertList";

const Root = ({store}) => {
    store.dispatch(fetchSettings());
    return (
        <Provider store={store}>
            <Router>
                <div className="sku-system-container">
                    <AlertList />
                    <Route exact path="/" render={() => (
                        <Redirect to={TAB_SKUS.path} />
                    )} />
                    <Route path="/" component={TabList} />
                    <div className="container-fluid">
                        <Route path={TAB_SKUS.path} component={SKUSystemTab} />
                        <Route path={TAB_BY_COLOR.path} component={UPCByColorTab} />
                        <Route path={TAB_COLORS.path} component={ColorsTab} />
                        <Route path={TAB_MIXES.path} component={MixesTab} />
                        <Route path={TAB_GROUPS.path} component={GroupsTab} />
                        <Route path={TAB_CATEGORIES.path} component={CategoriesTab} />
                    </div>
                    <div style={{position: 'fixed', bottom: '0', right: '0', left: '0'}}>
                        Version: {version}
                    </div>
                </div>
            </Router>
        </Provider>
    );
};

Root.propTypes = {
    store: PropTypes.object.isRequired
};

export default Root;
