import React, {Component} from 'react';
import PropTypes from 'prop-types';
import DataTableFilter from '../chums-components/DataTableFilter';
import {ellipses} from '../utils';
import GTIN from '../GTIN';
import {connect} from 'react-redux';
import ProgressBar from "../chums-components/ProgressBar";
import TrimmedText from "./TrimmedText";
import SortableTable from "../chums-components/SortableTable";
import {} from '../actions/colorUPC';
import {assignNextColorUPC} from "../actions/sku";

const AssignableColorUPC = ({upc = '', onClick}) => {
    return (upc || '') === ''
        ? (
            <button className="btn btn-outline-primary btn-xs" onClick={onClick}>
                <span className="material-icons md-12">navigate_next</span>
            </button>
        )
        : GTIN.format(upc)
};

const tableFields = [
    {field: 'company', title: 'Co'},
    {field: 'ItemCode', title: 'Item'},
    {field: 'ItemCodeDesc', title: 'Desc', render: (row) => (<TrimmedText text={row.ItemCodeDesc}/>)},
    {field: 'ProductType', title: 'PT'},
    {field: 'ProductLine', title: 'PL'},
    {field: 'UDF_UPC', render: (row) => GTIN.format(row.UDF_UPC), title: 'UPC', className: 'upc'},
    {
        field: 'UDF_UPC_BY_COLOR',
        render: (row) => GTIN.format(row.UDF_UPC_BY_COLOR),
        title: 'Color UPC',
        className: 'upc'
    },
];

class SKUItemList extends Component {
    static propTypes = {
        list: PropTypes.array.isRequired,
        isAdmin: PropTypes.bool,
        loading: PropTypes.bool,
        assignNextColorUPC: PropTypes.func.isRequired,
    };

    static defaultProps = {
        list: [],
        loading: false,
        isAdmin: false,
    };

    state = {
        selected: null,
        filter: '',
        showInactive: false,
        group: {},
        rowsPerPage: 10,
        page: 1,
    };

    constructor(props) {
        super(props);
        this.onChangeFilter = this.onChangeFilter.bind(this);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.isAdmin && !prevProps.isAdmin) {
            tableFields
                .filter(f => f.field === 'UDF_UPC_BY_COLOR')
                .map(field => field.render = (row) => (
                    <AssignableColorUPC upc={row.UDF_UPC_BY_COLOR} onClick={() => this.onClickNewByColorUPC(row)}/>
                ));
        }
    }

    onChangeFilter(val) {
        this.setState({filter: val});
    }

    onClickNewByColorUPC(item, ev) {
        this.props.assignNextColorUPC(item);
    }

    render() {
        const {list, loading} = this.props;
        const {filter, showInactive, rowsPerPage, page} = this.state;


        let reFilter;
        try {
            reFilter = new RegExp(filter, 'i');
        } catch (err) {
            reFilter = /./i;
        }
        const rows = list
            .filter(item => showInactive || item.active)
            .filter(item => filter.trim() === ''
                || reFilter.test(item.ItemCode) || reFilter.test(item.ItemCodeDesc)
                || reFilter.test(item.UDF_UPC) || reFilter.test(item.UDF_UPC_BY_COLOR));
        return (
            <div>
                <DataTableFilter filter={filter} showInactive={showInactive}
                                 onChangeFilter={this.onChangeFilter}
                                 onToggleInactive={() => {
                                     this.setState({showInactive: !showInactive})
                                 }}>
                    <h3>Sage Items</h3>
                </DataTableFilter>
                {loading && <ProgressBar striped={true} style={{height: '5px'}}/>}
                <SortableTable data={rows} fields={tableFields} displayPageSelectorInline={false}
                               page={page} onChangePage={(page) => this.setState({page})}
                               rowsPerPage={rowsPerPage}
                               onChangeRowsPerPage={(rowsPerPage) => this.setState({rowsPerPage, page: 1})}
                               defaultSort="ItemCode" selected={null}
                               rowClassName={(row) => ({'table-warning': !row.active})}/>
            </div>
        )
    }

}

const mapStateToProps = (state, ownProps) => {
    const {app, sku, groups} = state;
    const {items, selected, loading, selectedGroup, itemsLoading} = sku;
    const {isAdmin} = app;
    return {list: items, selected, loading: itemsLoading, selectedGroup, isAdmin, groups};
};

const mapDispatchToProps = {
    assignNextColorUPC,
};

export default connect(mapStateToProps, mapDispatchToProps)(SKUItemList);
