import React, {Component} from 'react';
import PropTypes from 'prop-types';
import ColorEditor from './ColorEditor';
import {ellipses} from '../utils';
import DataTableFilter from '../chums-components/DataTableFilter';
import {connect} from "react-redux";
import {fetchColors, saveColor, selectColor, updateColor} from "../actions/colors";
import TrimmedText from "./TrimmedText";
import SortableTable from "../chums-components/SortableTable";
import ProgressBar from "../chums-components/ProgressBar";

const tableFields = [
    {field: 'code', title: 'Code'},
    {field: 'description', title: 'Description'},
    {field: 'notes', title: 'Notes', render: ({notes}) => (<TrimmedText text={notes} length={65}/>)}
];

class ColorsTab extends Component {
    static propTypes = {
        list: PropTypes.array.isRequired,
        selected: PropTypes.object,
        loading: PropTypes.bool,
        isAdmin: PropTypes.bool,
    };

    state = {
        filter: '',
        showInactive: false,
        rowsPerPage: 10,
        page: 1,
    };

    constructor(props) {
        super(props);
        this.onChangeFilter = this.onChangeFilter.bind(this);
        this.onSelectColor = this.onSelectColor.bind(this);
        this.onClickNew = this.onClickNew.bind(this);
        this.onToggleInactive = this.onToggleInactive.bind(this);
        this.onChangeColor = this.onChangeColor.bind(this);
        this.onSaveColor = this.onSaveColor.bind(this);
        this.onLoadColors = this.onLoadColors.bind(this);
    }

    componentDidMount() {
        this.onLoadColors();
    }

    onLoadColors() {
        this.props.fetchColors();
    }

    onToggleInactive() {
        this.setState({
            showInactive: !this.state.showInactive
        });
    }

    onChangeFilter(filter) {
        this.setState({filter});
    }

    onSelectColor(color) {
        this.props.selectColor(color);
    }

    onClickNew() {
        this.onSelectColor({});
    }

    static renderNotes(text) {
        return (
            <span>{ellipses(text, 45)}</span>
        )
    }


    onChangeColor(field, value) {
        this.props.updateColor(field, value);
    }

    onSaveColor() {
        if (this.props.isAdmin) {
            this.props.saveColor(this.props.selected);
        }
    }


    render() {
        const {list, loading, selected, isAdmin} = this.props;
        const {filter, showInactive, rowsPerPage, page} = this.state;
        let reFilter;
        try {
            reFilter = new RegExp('\\b' + filter, 'i');
        } catch (err) {
            reFilter = /./i;
        }
        const colors = list
            .filter(color => showInactive || color.active)
            .filter(color => filter.trim() === '' || reFilter.test(color.code) || reFilter.test(color.description) || reFilter.test(color.notes || ''));


        return (
            <div className="row">
                <div className="col-sm-8">
                    <DataTableFilter filter={filter} showInactive={showInactive}
                                     onReload={this.onLoadColors}
                                     onChangeFilter={this.onChangeFilter}
                                     onToggleInactive={this.onToggleInactive}>
                        <button type="btn" className="btn btn-sm btn-outline-secondary" onClick={this.onClickNew}>
                            New Color
                        </button>
                    </DataTableFilter>
                    {loading && <ProgressBar striped={true} style={{height: '5px'}}/>}
                    <SortableTable fields={tableFields} data={colors} defaultSort="code" onSelect={this.onSelectColor}
                                   page={page} onChangePage={(page) => this.setState({page})}
                                   rowsPerPage={rowsPerPage}
                                   onChangeRowsPerPage={(rowsPerPage) => this.setState({rowsPerPage, page: 1})}/>
                </div>
                <div className="col-sm-4">
                    <ColorEditor color={selected || {}} onChange={this.onChangeColor} onSave={this.onSaveColor}
                                 isAdmin={isAdmin}/>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    const {list, selected, loading} = state.colors;
    const {isAdmin} = state.app;
    return {list, selected, loading, isAdmin};
};

const mapDispatchToProps = {
    fetchColors,
    saveColor,
    selectColor,
    updateColor
};

export default connect(mapStateToProps, mapDispatchToProps)(ColorsTab);
