import {
    FETCH_SKU_ITEMS,
    FETCH_SKU_LIST,
    RECEIVE_SKU, RECEIVE_SKU_ITEM, RECEIVE_SKU_ITEMS,
    RECEIVE_SKU_LIST,
    SELECT_SKU, SELECT_SKU_GROUP,
    UPDATE_SKU
} from "../constants";
import {fetchGET, fetchPOST} from "../chums-components/fetch";
import {setAlert} from "./app";


export const selectSKU = (sku = {id: 0}) => (dispatch) => {
    dispatch({type: SELECT_SKU, sku});
    if (!sku.id) {
        return;
    }
    const url = '/node-dev/sku/base/:id'.replace(':id', sku.id || 0);
    fetchGET(url)
        .then(res => {
            const [sku] = res.result;
            sku.notes = sku.notes || '';
            sku.key = sku.id;
            dispatch({type: RECEIVE_SKU, sku: sku || {}});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error loading SKU: ${err.message}`}));
        });
    dispatch(loadSKUItems(sku.sku));
};

export const fetchSKUList = (group_id) => (dispatch) => {
    const url = '/node-dev/sku/base/group/:id'.replace(':id', group_id);
    dispatch({type: FETCH_SKU_LIST});
    fetchGET(url)
        .then(res => {
            const list = res.result
                .map(sku => ({...sku, notes: sku.notes || '', key: sku.id}));
            dispatch({type: RECEIVE_SKU_LIST, list});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error loading sku list: ${err.message}`}));
        });
};

export const updateSKU = (field, value) => ({type: UPDATE_SKU, field, value});

export const saveSKU = (sku) => (dispatch, getState) => {
    const {app} = getState();
    if (!app.isAdmin) {
        return;
    }
    const url = '/node-dev/sku/base';
    fetchPOST(url, sku)
        .then(res => {
            dispatch({type: RECEIVE_SKU, sku: res.result[0]});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error saving color: ${err.message}`}));
        });
};

export const loadSKUItems = (sku) => (dispatch) => {
    const url = '/node-dev/sku/:sku'.replace(':sku', sku);
    dispatch({type: FETCH_SKU_ITEMS});
    fetchGET(url)
        .then(res => {
            const items = res.result
                .map(item => ({
                    ...item,
                    key: item.ItemCode,
                    active: !(item.InactiveItem === 'Y' || item.ProductType === 'D'),
                    notes: item.notes || ''
                }));
            dispatch({type: RECEIVE_SKU_ITEMS, items});
        })
        .catch(err => {
            dispatch(setAlert({message: `Error loading sku item list: ${err.message}`}));
        });
};

export const selectSKUGroup = (id) =>(dispatch, getState) => {
    const state = getState();
    const [group = {id: 0}] = state.groups.list.filter(g => g.id.toString() === String(id));
    dispatch({type: SELECT_SKU_GROUP, group});
    dispatch(fetchSKUList(id));
};


export const assignNextColorUPC = (item) => (dispatch, getState) => {
    const {app} = getState();
    if (!app.isAdmin) {
        return;
    }
    fetchGET('/node-dev/sku/by-color/next')
        .then(res => {
            if (!res.result) {
                throw new Error('Unable to fetch next Color UPC');
            }
            const upc = res.result;
            const data = {...item, UDF_UPC_BY_COLOR: upc, upc};
            return fetchPOST('/node-dev/sku/by-color', data);
        })
        .then(res => {
            const [item] = res.result;
            item.key = item.ItemCode;
            item.active = !(item.InactiveItem === 'Y' || item.ProductType === 'D');
            item.notes = item.notes || '';
            dispatch({type: RECEIVE_SKU_ITEM, item});
            const data = {
                Company: item.company,
                ItemCode: item.ItemCode,
                UDF_UPC_BY_COLOR: item.upc,
                action: 'update',
                id: item.id,
            };
            return fetchPOST('/sage/api/item-upc.php', data);
        })
        .then(res => {
            const {post} = res;
            return fetchPOST('/node-dev/sku/by-color/update-item', post);
        })
        .then(res => {
            const [item] = res.result;
            item.key = item.ItemCode;
            item.active = !(item.InactiveItem === 'Y' || item.ProductType === 'D');
            item.notes = item.notes || '';
            dispatch({type: RECEIVE_SKU_ITEM, item});
        })
        .catch(err => {
            dispatch(setAlert({message: `Unable to assign color UPC: ${err.message}`}));
        });
};
